*{
    background-color: black;
    color : white;
}
#title{
    font-weight: 800;
    font-size: 100px;
    text-align: center;
}
#root {
    display: flex;
    flex-direction: row;
}

#Homemovie {
    display : inline-block;
    width : 600px;
    height : 400px;
    margin : 0;
}
#Homemovie div:first-child {
    display: inline-block;
    width : 250px;
    height : 350px;
    margin : 0px;
}

#Homemovie div:last-child {
    display: inline-block;
    vertical-align: top;
    width : 300px;
    height : 350px;
}